import React from 'react';
import styled from "styled-components";
import Pop from './pop';
import Poptext from './Poptext';
import Poph2 from './Poph2';
import {  Link  } from "react-router-dom";

function PageSkills() {
    return(
        <div className="Home">
            <p>Skills</p>
            <Pop>{'Skills'}</Pop>
            <Poptext>{'実際の制作物は'}<SLink to="/PageWorks">Works</SLink>{'をご参照ください'}</Poptext>
            <Poph2>{'プログラミング({}内は使用可能なフレームワーク)'}</Poph2>
            <Poptext>
            {'JavaScript{React,express,babylon}'}<br />
            {'C言語'}<br />
            {'Python{django}'}<br />
            {'Java'}<br />
            {'HTML,CSS'}<br /><br />
            {'React,express,djangoなどを用いたフルスタックでのweb制作、Babylonを使用してのWebGL、CやPythonを用いたマイコンの電子制御プログラムの作成などが可能です'}<br /></Poptext><br />
            <Poph2>{'音楽制作'}</Poph2><Poptext>{'Studio Oneを使用しての打ち込み及びギター・ピアノなどの楽器を使った楽曲制作を行っています'}<br />{'バンドサウンドやピアノ・バイオリンなどのアコースティック系の楽曲制作を得意としていますが、他にも様々なジャンルの曲に対応が可能です'}</Poptext><br />
            <Poph2>{'CG制作'}</Poph2><Poptext>{'Blenderを用いたCG画像や簡単なアニメーションの作成を行っています'}<br />{'また、Unityと組み合わせてVRchat,Clusterといった主要なメタバースプラットフォームでのワールド制作も可能です'}<br /></Poptext><br />
            <Poph2>{'動画編集'}</Poph2><Poptext>{'Aviutlを使用し、字幕・テロップ入れ,カット編集,簡易的なアニメーションの作成などが可能です'}</Poptext><br />
            <Poph2>{'写真撮影'}</Poph2><Poptext>{'Canon EOS RPを使用した人物撮影,風景撮影を行っています'}</Poptext><br />
        </div>
    );
}

const SLink = styled(Link)`
    color: aliceblue;
`;

export default PageSkills;